import FileItem from "jx/editor/editionitems/FileItem";
export default class FontItem extends FileItem {
    constructor(dataObject) {
        dataObject.data.asset.type = "FontPack";
        super(Object.assign({
            icoSymbol: "file-text-o",
            actions: [],
            informations: ["asset"],
            backgroundColor: "#dcfffd"
        }, dataObject));
    }
}
